import React from 'react'



export default function GoodsItem(props)
{

    const {full_background, name, description, price, id, add_order = Function.prototype, order} = props;
    return (
        
            <div className="card" id={id}>
                <div className="card-image">
                <img
                    src={ full_background }
                    onError={ ( e ) => { e.target.src = "ph.png" } }
                    alt={ name } />
                </div>
                <div className="card-content">
                    <span className="info-card">{description}</span>     
                </div>
                <div onClick={() => add_order(order)} className="card-action">
                    <span className="up">{price} Руб. </span>
                    <button className="btn">Купить</button>
                </div>
            </div>

    )
}