import './Content.css'
import { API_KEY, API_URL } from "../../config.js"
import React, { useState, useEffect } from 'react'
import { Preloader } from "../Preloader/Preloader.jsx"
import GoodsList from "../Goods/Goods_list.jsx"
import Cart from "./Cart.jsx"
import ModalCart from '../modalcart/ModalCart.jsx'


export default function Content()
{
    const [ goods, setgoods ] = useState( [] ),
        [ loading, setloading ] = useState( true ),
        [ order, setOrder ] = useState( [] ), // список добавления в корзину
        [ opencart, setOpencart ] = useState( false ), // видем мы корзину или нет
        [ showon, setshowon ] = useState( false ),
        [ nameshow, setnameshow ] = useState( " " )

    useEffect( () =>
    {
        if ( showon ) {
            setTimeout( () => { setshowon( !showon ) }, 1500 )
        }
    }, [nameshow] )
    const add_order = ( item ) =>
    {
        setshowon( !showon )
        setnameshow( item.name )
        // получает индекс элемента в массиве если есть то его номер, если нет элемента то -1
        const itemIndex = order.findIndex( ( orderItem ) => orderItem.id === item.id )
        if ( itemIndex < 0 ) { // если элемента нет то добавляем его в корзину

            const newItem = {
                ...item,
                quantity: 1
            }
            setOrder( [ newItem, ...order ] )
        } else { // иначе проверяем какой это элемент

            const newOrder = order.map( ( items, index ) =>
            {
                if ( index === itemIndex ) { // если элемент равен его индексу то выполняется +1 к количеству в корзине
                    return {
                        ...items,
                        quantity: items.quantity + 1
                    }
                } else { // иначе возвращает элемент массива
                    return items
                }
            } )
            setOrder( newOrder )
        }
    }

    // Загрузка данных
    useEffect( () =>
    {
        fetch( API_URL, {
            headers: { 'Authorization': API_KEY },
        } ).then( response => response.json() )
            .then( data =>
            {
                data.featured && setgoods( data.featured )
                setloading( false )
            } )
    }, [] )

    function OpenCarts()
    {
        setOpencart( !opencart )
        if ( !opencart ) {
            document.body.style.overflowY = "hidden";
        } else {
            document.body.style.overflowY = "scroll";
        }
    }

    function delCart( id )
    {
        const newOrder = order.filter( el => el.id !== id )
        setOrder( newOrder )
    }

    function encriment( id )
    {
        const newOrder = order.map( ( item ) =>
        {
            if ( item.id === id ) { // если элемент равен его индексу то выполняется +1 к количеству в корзине
                return {
                    ...item,
                    quantity: item.quantity + 1
                }
            } else { // иначе возвращает элемент массива
                return item
            }
        } )
        setOrder( newOrder )
    }

    function decriment( id, q )
    {
        if ( q > 1 ) {
            const newOrder = order.map( ( item ) =>
            {
                if ( item.id === id ) { // если элемент равен его индексу то выполняется +1 к количеству в корзине
                    return {
                        ...item,
                        quantity: item.quantity - 1
                    }
                } else { // иначе возвращает элемент массива
                    return item
                }
            } )
            setOrder( newOrder )
        }
        if ( q === 1 ) {
            delCart( id )
        }
    }

    return (
        <>
            {opencart &&  <div className="overlay" ></div> }
            <div className='content'>
                { showon && <div className="showon">{ nameshow } добавлен в корзину</div> }
                <Cart OpenCarts={ OpenCarts } quantity={ order.length } />
                { opencart &&
                    <ModalCart
                    key="ModalCart"
                        order={ order }
                        OpenCarts={ OpenCarts }
                        delCart={ delCart }
                        encriment={ encriment }
                        decriment={ decriment }
                    /> }
                { loading ? <Preloader /> :
                    <GoodsList
                        goods={ goods }
                        add_order={ add_order }
                    /> }
            </div>
        </>
    )
}
