


export default function CartItems(props)
{
    const { name, price, quantity, delCart= Function.prototype, id, decriment = Function.prototype, encriment = Function.prototype } = props;
    return (
        <div className="cartItems">
                <span>{name}</span>
                <span className="minus" onClick={() => decriment(id, quantity)}> &#8210; </span>
                <span> x{quantity}</span>
                <span className="plus" onClick={() => encriment(id)}> &#10010; </span>
                =
                <span>{price * quantity} руб.</span>
                <i onClick={() => delCart(id)} className="Small material-icons minus">close</i>
            
        </div>
    )
    
}