import './ModalCart.css'
import CartItems from './CartItems.jsx'
import React, { useState, useEffect } from 'react'

export default function ModalCart( { order, OpenCarts = Function.prototype, delCart = Function.prototype, decriment = Function.prototype, encriment = Function.prototype } )
{
    const [ summa, setSumma ] = useState( 0 )

    useEffect( () =>
    {
        let sum = 0;
        order.map( (el) => sum += el.quantity * el.price)
        setSumma( sum )
    }, [ order ] )



    return (
        <>
            <div className="ModalCart">
                <div className="Fix jsb teal accent-4"> <span>Корзина</span> <i onClick={ OpenCarts } className="Medium material-icons">close</i></div>
                <div className="elementmodal">
                    { order.length > 0 ?
                        order.map( el =>
                            <div className="elementItem">
                                <CartItems
                                    key={ el.id + "cart" }
                                    name={ el.name }
                                    price={ el.price }
                                    id={ el.id }
                                    quantity={ el.quantity }
                                    delCart={ delCart }
                                    encriment={ encriment }
                                    decriment={ decriment }
                                /> </div>
                        ) : ( <div className="elementItem"> Корзина пуста</div> )
                    }
                </div>
                <div className="Fix hfix teal accent-4">Общая стоимость: { summa } руб. <button className="btn Fixis orange lighten-1">Заказать</button></div>
            </div>
        </>
    )

}