import React from 'react'
import GoodsItem from './Goods_Item.jsx'
import "./Goods.css"



export default function GoodsList(props){
    const { goods = [], add_order} = props;
    return (
        <div className="grid-col">
            {goods ? goods.map(item => (
                <GoodsItem
                    order={item}
                    key={item.id}
                    add_order={add_order}
                    {...item}
                />
            )) : <h3>Ошибка вывода</h3>}
    </div>
    )


}